import React from "react"
import Layout from "../components/layout"
import BootstrapTable from 'react-bootstrap-table-next'
import { useTranslation } from 'react-i18next'
import { Link } from 'gatsby'
import "../styles/table.css"
import SEO from "../components/seo"
import Banner from '../components/banner';
import CountryIcon from "../../assets/menu_icon_countries.svg"
import ToolkitProvider, { Search } from 'react-bootstrap-table2-toolkit'
import filterFactory, { multiSelectFilter } from 'react-bootstrap-table2-filter'

const { SearchBar } = Search

const CountriesPage = ({ pageContext: { countriesTable } }) => {
  const { t, i18n } = useTranslation()

  const countryTranslations = t('countries.names', { returnObjects: true })

  let continentFilter;

  const handleChange = (event) => {
    continentFilter([event.target.value]);
  }

  const continentTranslations =  {
      "EU": t('countries.continents.eu'),
      "AS": t('countries.continents.as'),
      "AF": t('countries.continents.af'),
      "NA": t('countries.continents.na'),
      "SA": t('countries.continents.sa'),
      "OC": t('countries.continents.oc')
    }

  return (
    <Layout>
      <SEO title={ t('countries.title') } lang={ i18n.language } />
      <Banner
          title={ t('countries.title') }
          subtitle={ t('countries.subtitle') }
          image={'https://images.bible.cloud/fab/banners/countries.jpg'}
          icon={CountryIcon}
          breadcrumbs={[
            {path: '/', title: 'home'},
            {path: '#', title: t('countries.title')}
          ]}
      />
      <div className="row">
        <div className="medium-8 columns centered tiles-table thead-hide">
          <ToolkitProvider
              keyField="id"
              data={countriesTable}
              bordered={false}
              columns={[
                {
                  dataField: 'id',
                  text: 'ID',
                  hidden: true,
                  keyField: true
                },
                {
                  dataField: 'name',
                  text: 'Name',
                  sort: true,
                  formatter: (cell, row) =>
                      <Link to={`/countries/${ row.id }`}>
                        <svg className="icon"><use xlinkHref={`/flags.svg#${ row.id }`}></use></svg>
                        { countryTranslations[row.id] }
                      </Link>
                },
                {
                  dataField: 'continent',
                  text: 'Continent',
                  formatter: cell => continentTranslations[cell],
                  filter: multiSelectFilter({
                    options: continentTranslations,
                    getFilter: (filter) => {
                      // continentFilter was assigned once the component has been mounted.
                      continentFilter = filter;
                    }
                  })
                },
              ]}
              search
          >
            {
              props => (
                  <div>
                    <div className="row">
                      <div className="medium-6 columns">
                      <SearchBar {...props.searchProps} delay={100}
                                 placeholder={t('search.filterBy') + countriesTable.length  + ' ' + t('countries.title')} />
                      </div>
                      <div className="medium-6 columns">
                        <label>
                        <select id="continents" onChange={ handleChange }>
                          <option value="">{ t('search.filterBy') + ' ' + t('countries.continent') }</option>
                          <option value="EU">{t('countries.continents.eu')}</option>
                          <option value="AS">{t('countries.continents.as')}</option>
                          <option value="AF">{t('countries.continents.af')}</option>
                          <option value="NA">{t('countries.continents.na')}</option>
                          <option value="SA">{t('countries.continents.sa')}</option>
                          <option value="OC">{t('countries.continents.oc')}</option>
                        </select>
                        </label>
                      </div>
                    </div>
                    <BootstrapTable {...props.baseProps}
                      filter={ filterFactory() }
                      defaultSorted={[{
                        dataField: 'name',
                        order: 'asc'
                      }]}
                    />
                  </div>
              )
            }
          </ToolkitProvider>
        </div>
      </div>

    </Layout>
  );
};

export default CountriesPage
